.gallery__dishes-title {
  font-size: 40px;
  line-height: 46px;
  font-weight: 400;
  text-align: center;
  width: 154px;
  margin: 71px auto 82px auto;
}

.gallery__dishes-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.gallery__dishes-item {
  width: 290px;
  height: 274px;
  background-repeat: no-repeat;
  background-size: cover;

  &--wine {
    background-image: url("../img/mobile/wine.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/mobile/wine@2x.png");
    }
  }

  &--coffe {
    background-image: url("../img/mobile/coffe.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/mobile/coffe@2x.png");
    }
  }

  &--soup {
    background-image: url("../img/mobile/soup.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/mobile/soup@2x.png");
    }
  }
}

.gallery__cooks-title {
  font-size: 40px;
  line-height: 46px;
  font-weight: 400;
  text-align: center;
  width: 144px;
  margin: 71px auto 80px auto;
}

.gallery__cooks-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 18px;
  margin: 0 0 21px 0;
  padding: 0;
  list-style: none;
}

.gallery__cooks-item {
  width: 288px;
  height: 380px;
  background-repeat: no-repeat;
  background-size: contain;

  &--sushimaker {
    background-image: url("../img/mobile/cook1.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/mobile/cook1@2x.png");
    }
  }

  &--pizzamaker {
    background-image: url("../img/mobile/cook2.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/mobile/cook2@2x.png");
    }
  }

  &--confectioner {
    background-image: url("../img/mobile/cook3.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/mobile/cook3@2x.png");
    }
  }
}

@media (min-width: $tabletW) {
  .gallery__dishes-title {
    width: 280px;
  }

  .gallery__dishes-list {
    display: grid;
    grid-template-columns: min-content min-content;
    justify-content: space-evenly;
  }

  .gallery__cooks-title {
    width: 270px;
  }

  .gallery__cooks-list {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    margin: 0 0 45px 0;
  }
}
